// @flow
import { observable } from 'mobx'
import guid from "./guid"
import Unit from "./Unit"
import type { Coordinate } from "./GameTypes"

class Bullet {
    id: number 
    willHit: boolean
    sourceUnit: Unit
    targetUnit: Unit 
    sourceCoordinate: Coordinate
    currentCoordinate: Coordinate
    displayOffset: Coordinate
    velocity: Coordinate

    @observable currentCoordinate: Coordinate;
    @observable angle: number;

    constructor({ willHit, sourceUnit, targetUnit, sourceCoordinate, currentCoordinate, velocity, displayOffset }:
        { 
            willHit: boolean, sourceUnit: Unit, targetUnit: Unit, 
            sourceCoordinate: Coordinate, 
            velocity: Coordinate,
            currentCoordinate: Coordinate,
            displayOffset: Coordinate
        }) {
        this.id = guid()
        this.willHit = willHit
        this.sourceUnit = sourceUnit
        this.targetUnit = targetUnit
        this.sourceCoordinate = sourceCoordinate
        this.currentCoordinate = currentCoordinate
        this.velocity = velocity
        this.displayOffset = displayOffset
    }
}

export default Bullet



