// @flow
import React, { Component } from 'react';
import Game from "../lib/Game"

class GameControls extends Component {

    props: {
        game: Game, 
        reset: () => void
    }

    render() {
        const { game, reset, game: { tickCount } } = this.props
        return (
            <div className="controls">
                <button onClick={() => game.pause()}>Pause</button>
                <button onClick={() => game.play()}>Play</button>
                <button onClick={() => game.slowMotion()}>Slow Motion</button>
                <button onClick={() => game.fastMotion()}>Speedup</button>
                <button onClick={() => reset()}>Reset</button>
                T-{ tickCount }
            </div>
        )
    }
}

export default GameControls



